<template>
  <div class="page">
    <div class="container">
      <div class="content">
        <h2>{{ getTranslatedText('setWeeklyGoal') }}</h2>
        <hr>
        <div class="input-group">
          <label for="weeklyGoal">{{ getTranslatedText('goal') }}</label><br>
          <textarea id="weeklyGoal" v-model="weeklyGoal" rows="4" 
                    :class="{ 'error-border': errorSubmitting }" 
                    ref="weeklyGoalField"></textarea>
        </div><br>
        <button @click="submitForm">{{ getTranslatedText('submit') }}</button>
        <h5 v-if="errorFetchingDate">{{ getTranslatedText('fetchError') }}</h5>
      </div>
      <div v-if="showPopup" class="popup">
        <h2>{{ getTranslatedText('formSuccess') }}</h2>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { mapState } from 'vuex';
import { API_BASE_URL } from '@/config.js';

export default {
  data() {
    return {
      weeklyGoal: '',
      showPopup: false,
      errorSubmitting: false,
      errorFetchingDate: false,
    };
  },
  computed: {
    ...mapState(['language'])
  },
  methods: {
    getTranslatedText(key) {
      const translations = {
        setWeeklyGoal: { en: "Set Weekly Goal", fr: "Définir l'objectif pour la Semaine" },
        goal: { en: "Goal", fr: "Objectif" },
        submit: { en: "Submit", fr: "Soumettre" },
        fetchError: { en: "Error fetching date. Please check your internet connection.", fr: "Erreur lors du chargement de la date. Veuillez vérifier votre connexion Internet." },
        formSuccess: { en: "Form submitted successfully!", fr: "Formulaire soumis avec succès !" },
      };
      return translations[key][this.language];
    },
    submitForm() {
      const token = localStorage.getItem('token');
      const headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      };
      const formData = { weeklyGoal: this.weeklyGoal };

      axios.post(`${API_BASE_URL}/week/weekly`, formData, { headers })
        .then(response => {
          console.log('Form submitted successfully:', response.data);
          this.weeklyGoal = '';
          this.showPopup = true;
          setTimeout(() => {
            this.showPopup = false;
            this.$router.push('/admin/analytics');
          }, 3000);
          this.errorSubmitting = false;
        })
        .catch(error => {
          console.error('Error submitting form:', error);
          this.errorSubmitting = true;
          this.shakeInput();
        });
    },
    shakeInput() {
      const weeklyGoalField = this.$refs.weeklyGoalField;
      if (weeklyGoalField) {
        weeklyGoalField.classList.add('shake');
        setTimeout(() => weeklyGoalField.classList.remove('shake'), 500);
      }
    }
  }
};
</script>
  
